* {
    margin: 0;
    padding: 0;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.itemList {
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 5px;

}



img {
    height: 300px;
    width: 300px
}

form {
    display: flex;
    justify-content: center
}

header {
    background-color: rgb(189, 203, 189);
    border: black 2px solid;
    padding: 20px;
    margin: 5px;
}

footer {
    background-color: rgb(165, 165, 186);
    border: black 2px solid;
    padding: 20px;
    margin: 5px;
}


nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}

nav ul {
    list-style: none;
    display: flex;

}

nav ul li {
    margin: 0px 10px;

}

nav ul li a {
    text-decoration: none;

}

li a:hover {

    color: white;
}




